.card-wrapper {
  position: relative;
  background-color: #505765;
  cursor: pointer;
  border-radius: 10px;

  &.title {
    cursor: default;
  }

  &.back {
    background-color: #636c7f;
  }
}

.card-body {
  padding: 40px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.25);
  width: 800px;
  height: 250px;
  font-family: Geologica, sans-serif;;
}

.card-body:hover {
  box-shadow: 0 16px 32px 0 rgba(0, 0, 0, 0.25);
}

.card-header-wrapper {
  position: absolute;
  top: 0;
  width: 100%;
  margin-top: 15px;

  > .card-header {
    margin: 0 20px;
    display: grid;
    grid-auto-flow: column;
    justify-content: space-between;
    align-items: center;
  }

  > * a:hover {
    color: #ff5722;
  }
}

.card-footer-wrapper {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin-bottom: 15px;

  color: #b4b9c3;

  > .card-footer {
    margin: 0 20px;
    display: grid;
    grid-auto-flow: column;
    justify-content: space-between;
    align-items: center;
  }
}

.card-text {
  font-size: 4rem;
  margin: 0;
  max-width: 100%;
  max-height: 100%;
  overflow: scroll;
}

.card-action {
  opacity: 0.5;

  &:hover {
    opacity: 1;
  }
}
